<template>
  <div>
    <Header subtitle="Search movies"/>

    <b-container fluid>

      <b-form @submit="submitSearch">

        <b-form-group
            id="serach-input-group"
            label="Search:"
            label-for="search-input"
        >
          <b-form-input
              id="search-input"
              v-model.trim="$v.form.searchInput.$model"
              type="text"
              placeholder="Search movies by title..."
              :state="validateState('searchInput')"
              aria-describedby="input-search-feedback"
          ></b-form-input>
          <b-form-invalid-feedback v-if="!$v.form.searchInput.required"
                id="input-search-feedback">Search input is required.</b-form-invalid-feedback>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>

      </b-form>
    </b-container>

    <MovieSearchTable v-if="searchCount > 0" :searchCount="searchCount"  :searchTitle="searchTitle"/>
    <h2 v-else-if="searchCount == 0">No results found for: {{searchTitle}}</h2>

  </div>
</template>

<script>
  import Header from '@/components/Header.vue';
  import MovieSearchTable from '@/components/MovieSearchTable.vue';
  import { required } from "vuelidate/lib/validators";

  export default {
    name: 'SearchMovies',

    components: {
      Header,
      MovieSearchTable
    },

    data() {
      return {
        form : { searchInput : ''},
        searchCount: -1
      }
    },

    validations : {
      form : {searchInput : {required}}
    },

    methods: {
      validateState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      resetForm() {
        this.form = {
          searchInput: '',
        };

        this.$nextTick(() => {
          this.$v.$reset();
        });
      },

      submitSearch: function (e) {
        e.preventDefault();

        if(this.$v.form.$anyError){
          return;
        } else {
          fetch(`/api/movies/count/search?title=${this.form.searchInput}`, {
              method: 'GET',
              headers: {
                'Authorization': `Bearer ${localStorage.token}`
              }
            })
            .then(obj => obj.json())
            .then(res => {
              this.searchCount = res;
            });
            this.resetForm();
        }
      }
    }
  }

</script>

<style scoped>
  .pagination {
        justify-content: center;
    }
</style>